<template>
    <div v-show="isSingleMode" class="appStatusContent p-r">
        <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('appControl.keywordHolder')" >  
            </el-input>
            <i class="fa fa-search search-icon" aria-hidden="true"></i>
        </div>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="appOperation">
                <span slot="label">
                    <i class="fa fa-hand-pointer-o m-r-5" aria-hidden="true"></i>
                    {{$t('appControl.appOperation')}}
                </span>
                <div class="text-center noData" v-show="!appTypeShow">
                    <p>
                        <img src="@/assets/imgs/noData.png" alt="" style="">
                        {{$t('global.noData')}}
                    </p>
                    <el-button 
                    size="small" 
                    type="danger" 
                    @click="goAppMarket"  
                    >
                        {{$t('appControl.installApp')}}
                    </el-button>
                </div>
                <div class="m-b-20 m-r-15 cf" v-if="appTypeShow">
                    <el-radio-group v-model="appType" class="fr"  size="small">
                        <el-radio-button label="uninstallApp">
                            <i class="fa fa-crosshairs m-r-5" aria-hidden="true"></i>
                            {{$t('appControl.thirdApp')}}
                        </el-radio-button>
                        <el-radio-button label="system">
                            <i class="fa fa-dot-circle-o m-r-5" aria-hidden="true"></i>
                            {{$t('appControl.systemApp')}}
                        </el-radio-button>
                        <el-radio-button label="desktop">
                            <i class="fa fa-desktop m-r-5" aria-hidden="true"></i>
                            {{$t('appControl.desktopApp')}}
                        </el-radio-button>
                    </el-radio-group>
                </div>
                <card-temp
                :data="cardAppData"  
                :subIntro= "subIntro"
                :isAllShow = "false"
                :typeArray="appTypeArray"
                :typeKey="typeKey"
                headerBg="#efefef"
                >
                    <div class="card-header cf" slot="card-header" slot-scope="props">
                        <svg t="1571298273142" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3499" 
                        width="20" height="20" v-if ="props.item.isKioskApp == 'true'">
                        <path d="M891.3 98.2H700.8c-20.7 0-37.6 13.9-37.6 32.5s16.9 32.5 37.6 32.5H863v162.3c0 20.7 13.9 37.6 32.5 37.6s32.5-16.9 32.5-37.6V130.7c0-18.6-16.1-32.5-36.7-32.5z m-754.9 0h190.5c20.7 0 37.6 13.9 37.6 32.5s-16.9 32.5-37.6 32.5H164.7v162.3c0 20.7-14.8 37.6-33.4 37.6-18.2 0-33.4-16.9-33.4-37.6V130.7c0.5-18.6 17.8-32.5 38.5-32.5z m754.9 830.1H700.8c-20.7 0-37.6-14.8-37.6-33.4 0-18.6 16.9-33.4 37.6-33.4H863V699.2c0-20.7 13.9-37.6 32.5-37.6s32.5 16.9 32.5 37.6v194.9c0 18.6-16.1 34.2-36.7 34.2z m-754.9 0h190.5c20.7 0 37.6-14.8 37.6-33.4 0-18.6-16.9-33.4-37.6-33.4H164.7V699.2c0-20.7-14.8-37.6-33.4-37.6-18.2 0-33.4 16.9-33.4 37.6v194.9c0.5 18.6 17.8 34.2 38.5 34.2zM286 747h462.1V284.6H286V747z m51.1-411.3h360.3v360.5H337.1V335.7z m0 0" p-id="3500" fill="#3c8dbc"></path>
                        </svg>
                        <i class="fa fa-android fl p-5" aria-hidden="true" v-else></i>
                        
                        <i class="fa fa-ellipsis-v fr pointer p-5 c-gray disableOperate" aria-hidden="true" 
                        @click="disableOperation" 
                        v-if="whitelist&& props.item.inwhitelist == 'false'"></i>
                        <el-dropdown trigger="click" class="fr pointer" size="small" @command="handleCommand" v-else>
                            <span class="el-dropdown-link">
                                <i class="fa fa-ellipsis-v p-5 c-primary" aria-hidden="true" ></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item :command="{appInfo: props.item, id: 'startApp'}">
                                    <i class="fa fa-hand-o-up m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.launch')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'stopApp'}"

                                :disabled="disableAppArray.indexOf(props.item.package)>-1">
                                    <i class="fa fa-hand-paper-o m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.stop')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'enableApp'}"
                                :disabled="whitelist|| props.item.isKioskApp == 'true'">
                                    <i class="fa fa-check-circle m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.enable')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'disableApp'}"
                                :disabled="disableAppArray.indexOf(props.item.package)>-1|| whitelist|| props.item.isKioskApp == 'true'">

                                    <i class="fa fa-ban m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.disable')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'setKiosk'}"
                                :disabled="disableAppArray.indexOf(props.item.package)>-1|| props.item.isKioskApp == 'true'">
                                    <svg 
                                    t="1571298273142" 
                                    class="icon m-r-5" viewBox="0 0 1024 1024" version="1.1"
                                    xmlns="http://www.w3.org/2000/svg" p-id="3499" 
                                    width="12" height="12"
                                    v-if="disableAppArray.indexOf(props.item.package)>-1|| props.item.isKioskApp == 'true'">
                                    <path d="M891.3 98.2H700.8c-20.7 0-37.6 13.9-37.6 32.5s16.9 32.5 37.6 32.5H863v162.3c0 20.7 13.9 37.6 32.5 37.6s32.5-16.9 32.5-37.6V130.7c0-18.6-16.1-32.5-36.7-32.5z m-754.9 0h190.5c20.7 0 37.6 13.9 37.6 32.5s-16.9 32.5-37.6 32.5H164.7v162.3c0 20.7-14.8 37.6-33.4 37.6-18.2 0-33.4-16.9-33.4-37.6V130.7c0.5-18.6 17.8-32.5 38.5-32.5z m754.9 830.1H700.8c-20.7 0-37.6-14.8-37.6-33.4 0-18.6 16.9-33.4 37.6-33.4H863V699.2c0-20.7 13.9-37.6 32.5-37.6s32.5 16.9 32.5 37.6v194.9c0 18.6-16.1 34.2-36.7 34.2z m-754.9 0h190.5c20.7 0 37.6-14.8 37.6-33.4 0-18.6-16.9-33.4-37.6-33.4H164.7V699.2c0-20.7-14.8-37.6-33.4-37.6-18.2 0-33.4 16.9-33.4 37.6v194.9c0.5 18.6 17.8 34.2 38.5 34.2zM286 747h462.1V284.6H286V747z m51.1-411.3h360.3v360.5H337.1V335.7z m0 0" p-id="3500" fill="#bbbbbb"></path>
                                    </svg>
                                    <svg t="1571298273142" class="icon m-r-5"
                                    viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3499"
                                    width="12" height="12" v-else>
                                    <path d="M891.3 98.2H700.8c-20.7 0-37.6 13.9-37.6 32.5s16.9 32.5 37.6 32.5H863v162.3c0 20.7 13.9 37.6 32.5 37.6s32.5-16.9 32.5-37.6V130.7c0-18.6-16.1-32.5-36.7-32.5z m-754.9 0h190.5c20.7 0 37.6 13.9 37.6 32.5s-16.9 32.5-37.6 32.5H164.7v162.3c0 20.7-14.8 37.6-33.4 37.6-18.2 0-33.4-16.9-33.4-37.6V130.7c0.5-18.6 17.8-32.5 38.5-32.5z m754.9 830.1H700.8c-20.7 0-37.6-14.8-37.6-33.4 0-18.6 16.9-33.4 37.6-33.4H863V699.2c0-20.7 13.9-37.6 32.5-37.6s32.5 16.9 32.5 37.6v194.9c0 18.6-16.1 34.2-36.7 34.2z m-754.9 0h190.5c20.7 0 37.6-14.8 37.6-33.4 0-18.6-16.9-33.4-37.6-33.4H164.7V699.2c0-20.7-14.8-37.6-33.4-37.6-18.2 0-33.4 16.9-33.4 37.6v194.9c0.5 18.6 17.8 34.2 38.5 34.2zM286 747h462.1V284.6H286V747z m51.1-411.3h360.3v360.5H337.1V335.7z m0 0" p-id="3500" fill="#606266"></path>
                                    </svg>
                                    {{$t('appControl.setKiosk')}}
                                </el-dropdown-item>

                                <el-dropdown-item :command="{appInfo: props.item, id: 'cancelKiosk'}"
                                :disabled="disableAppArray.indexOf(props.item.package)>-1|| props.item.isKioskApp == 'false'">
                                    <svg t="1571735162764" class="icon m-r-5" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5560" 
                                        width="12" height="12" 
                                        v-if="disableAppArray.indexOf(props.item.package)>-1|| props.item.isKioskApp == 'false'">
                                        <path d="M853.333333 938.666667H682.666667v-85.333334h170.666666V682.666667h85.333334v256h-85.333334z m0-768H682.666667V85.333333h256v256h-85.333334V170.666667z m-682.666666 682.666666h170.666666v85.333334H85.333333V682.666667h85.333334v170.666666z m0-768h170.666666v85.333334H170.666667v170.666666H85.333333V85.333333h85.333334z m170.666666 256h341.333334v341.333334H341.333333V341.333333z" p-id="5561" fill="#bbbbbb"></path>
                                    </svg>
                                    <svg t="1571735162764" class="icon m-r-5" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5560" 
                                        width="12" height="12" v-else>
                                        <path d="M853.333333 938.666667H682.666667v-85.333334h170.666666V682.666667h85.333334v256h-85.333334z m0-768H682.666667V85.333333h256v256h-85.333334V170.666667z m-682.666666 682.666666h170.666666v85.333334H85.333333V682.666667h85.333334v170.666666z m0-768h170.666666v85.333334H170.666667v170.666666H85.333333V85.333333h85.333334z m170.666666 256h341.333334v341.333334H341.333333V341.333333z" p-id="5561" fill="#606266"></path>
                                    </svg>
                                    
                                    {{$t('appControl.cancelKiosk')}}
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                    <img :src="getSrc(props.item.raid)" :onerror="defaultImg" alt="no img" slot="card-body-img" slot-scope="props"/>
                </card-temp>       
            </el-tab-pane>
        </el-tabs>
    </div>
    
</template>


<script>
    import {appControl} from '@/assets/js/lwm2mMap'
    import {showSystemAppArray, disableAppArray} from "@/assets/js/constant"
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import { androidControl } from '@/assets/js/lwm2mMap';
    import {getRepoAppsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi';
    import {getRepoSrcApi} from "@/components/restfulapi/serverConfigApi"
    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import {singleInstallType, batchAppType} from "../../../assets/js/constant";
    import appHistory from "./history"
    import cardTemp from "../../../common/cardTemp"

    export default{
        name: 'singleAppcontrol',
        data(){
            return {
                activeName: "appOperation",
                startApp: '',
                stopApp: '',
                enableApp: '',
                disableApp: '',
                appTableData: [],
                appOptions: [],
                disableAppArray: disableAppArray,

                appGroups:[{label: "Third Part", value: "third"},{label: "System", value: "system"}],
                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                //history
                singleInstallType,
                //card
                appType: "uninstallApp",
                whitelist: false,
                listLoading: false,
                keywords: '',
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            appHistory,
 	        cardTemp
        },

        methods:{
            getSensorStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                let AppManagementInfoArray = [];
                let deviceAppArray = [];
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentId, appControl["getAllAppInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let apppackageinfo = JSON.parse(res.content.value);
                                let allAppData = apppackageinfo.data;
                                this.appOptions = allAppData;
                                if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                    allAppData.forEach((val) => {
                                        let uninstallAppObj = {
                                            type: 'uninstallApp',
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            version: val.versionName,
                                            address: "",
                                            raid: "",
                                            isKioskApp: val.isKioskApp,
                                            inwhitelist: val.inWhiteList,
                                        };
                                        if(val.type === "third"){
                                            AppManagementInfoArray.push(uninstallAppObj);
                                        }
                                        deviceAppArray.push(uninstallAppObj);  
                                    })
                                }
                            }
                            
                            this.getRepoApps(AppManagementInfoArray, deviceAppArray);  
                        }
                        
                          
                    })   
                })
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps
            getRepoApps(AppManagementInfoArray, deviceAppArray){
                if(
                    Object.prototype.toString.call(AppManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(deviceAppArray) != "[object Array]"
                ){
                    console.error("getRepoAppsparameter is not array")
                    return;
                }
                let token;
                let InstallAppManagementInfo = {};
                let UpgradeAppManagementInfo = {};
                this.listLoading = true;
                getRepoAppsApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installappopt = "";
                            let upgradeappopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    InstallAppManagementInfo = {
                                        type : "installApp", 
                                        darkname: val.repo.darkname,
                                        //install apk name
                                        appname: val.filename, 
                                        package: val.pkgname, 
                                        versioncode: val.versioncode, 
                                        version: val.versionname,
                                        address: val.address,
                                        raid: val.raid,
                                        isKioskApp: "false",
                                        inwhitelist: "false",
                                        };
                                    AppManagementInfoArray.push(InstallAppManagementInfo);
                         
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // pop latest app info
                                            AppManagementInfoArray.pop();
                                            deviceAppArray[index].address = val.address;
                                            deviceAppArray[index].raid = val.raid;
                                            if (Number(val.versioncode) > Number(deviceapp_val.versioncode)){
                                                // remove install app  
                                                // this.removeObjInArray(AppManagementInfoArray, val.pkgname);
                                                UpgradeAppManagementInfo = {
                                                    type : "upgradeApp", 
                                                    darkname: val.repo.darkname,
                                                    appname: val.filename, 
                                                    package: val.pkgname, 
                                                    versioncode: val.versioncode, 
                                                    version: val.versionname,
                                                    address: val.address,
                                                    raid: val.raid,
                                                    isKioskApp: "false",
                                                    inwhitelist: "false",
                                                    };
                                                    //push update app 
                                                AppManagementInfoArray.push(UpgradeAppManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                        this.appTableData = AppManagementInfoArray.reverse();
                    })
                    
                })
            },

            filterType(value, row){
                return row.type === value ;
            },
            actionAppSensor(cid, appdata){
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }

                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("appControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        execDeviceStatus(this.selectedAgentId, appControl[cid], appdata).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.activeName = "appStatusHistory";
                                    this.$refs.appHistory.intervalGetAppStatusHistory();
                                }else{
                                    _g.handleError(res)
                                }
                            })
                        })
                    }
                })
            },

            setAppSensor(cid, setSensorVal){
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!setSensorVal){
                    return;
                }
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                if(cid === "uninstallApp" || cid === "disableApp"  || cid === "stopApp" ){
                    _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("appControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                        if (willfunc) {
                            _g.openGlobalLoading();
                            setDeviceStatus(this.selectedAgentId, appControl[cid], setSensorVal).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "CHANGED"){
                                        this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                            if(cid === "uninstallApp"){
                                                this.getSensorStatus();
                                            }                             
                                        })
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                                
                            })
                        }
                    })
                }else{
                    _g.openGlobalLoading();
                    setDeviceStatus(this.selectedAgentId, appControl[cid], setSensorVal).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status == "CHANGED"){
                                this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                    if(cid === "setKiosk"|| cid === "cancelKiosk"){
                                        this.getSensorStatus();
                                    }                             
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                        
                    })
                }
            },

            isFinished(){
                if(this.selectedAgentId){
                    if(!this.isOffline){
                        this.getSensorStatus();

                    }

                }
            },

            appAction(cid, selectedAppData){

                let setsensorval, version;
                if(cid === "installApp" || cid === "upgradeApp"){
                    setsensorval = selectedAppData.address + "/" +  selectedAppData.appname;
                    version = selectedAppData.version;
                }else{
                    setsensorval = selectedAppData.package;
                    this.setAppSensor(cid, setsensorval);
                    return;
                }
                let taskname = this.singleInstallType+ (new Date()).getTime();
                let appdata = {
                    taskname,
                    filename: selectedAppData.appname,
                    pkgname: selectedAppData.package,
                    versioncode: selectedAppData.versioncode,
                    version: version,
                    downloadpath: setsensorval,
                }
                this.actionAppSensor(cid, appdata)
                
                
            },

            handleCommand(command){
                let type=command.id
                let pkgname;
                if(this.appType === "system"|| this.appType==="desktop"){
                    pkgname = command.appInfo.packageName;
                }else{
                    pkgname = command.appInfo.package;
                }
                this.setAppSensor(type, pkgname)
            },

            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
            },
            initData(){
                this.startApp = '';
                this.stopApp = '';
                this.enableApp = '';
                this.disableApp = '';
                this.appTableData = [];
                this.appOptions = [];
            },
            handleClick(tab){
                if(tab.name === "appStatusHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },

            //get whitelist server status
            getServerStatus(){
                let sensor_key = "whitelist"
                getDeviceStatus(this.selectedAgentId, androidControl[sensor_key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let status = res.content.value;
                            if(typeof(status) === "boolean"){
                                this[sensor_key] = status;
                            }else if(status === "true" || status === "false"){
                                this[sensor_key] = status === "true"? true: false;
                            }else{
                                this[sensor_key] = status;
                            }
                        }
                    })
                })
                
            },
            disableOperation(){
                this.$swal("", this.$t('appControl.appNotInWhitelist'), 'info', {button: this.$t('global.confirm')});
            },

            goAppMarket(){
                this.$router.push("/main/deploy/appcontrol");
            }
        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();

                    if(val&& !this.isOffline){
                        this.getSensorStatus();
                        this.getServerStatus();

                        // this.$refs.appHistory.intervalGetAppStatusHistory();
                    }
                }
            },
           
        },
        computed: {
            cardAppData(){
                if(this.appType === "uninstallApp"){
                    return this.appTableData;
                }else if(this.appType === "system" || this.appType=="desktop"){
                    return this.appOptions;
                }else{
                    return [];
                }
            },
            
            appTypeShow(){
                if(this.cardAppData.length >0){
                    return true;
                }else{
                    return false;
                }
                
            },

            subIntro(){
                if(this.appType === "uninstallApp"){
                    return ['appname', 'version'];
                }else if(this.appType === "system"|| this.appType === "desktop"){
                    return ['appName', 'versionName'];
                }else{
                    return [];
                }
            },
            appTypeArray(){
                if(this.appType === "uninstallApp"){
                    return ['uninstallApp'];
                }else if(this.appType === "system"){
                    return ["system"];
                }else if(this.appType === "desktop"){
                    return ["true"]
                }else{
                    return []
                }
            },

            typeKey(){
                if(this.appType == "desktop"){
                    return "isDesktopApp"
                }else{
                    return "type"
                }
            },

            isShowSearchInput(){
                if(this.activeName === "appMarket"){
                    return true;
                }else{
                    return false;
                }
            }

        }
    }
</script>

<style lang="scss" scoped>
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

    .search{
        position: absolute;
        top: 3px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



