                        <svg t="1571298273142" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3499" 
                        width="20" height="20" v-if ="props.item.isKioskApp == 'true'">
                        <path d="M891.3 98.2H700.8c-20.7 0-37.6 13.9-37.6 32.5s16.9 32.5 37.6 32.5H863v162.3c0 20.7 13.9 37.6 32.5 37.6s32.5-16.9 32.5-37.6V130.7c0-18.6-16.1-32.5-36.7-32.5z m-754.9 0h190.5c20.7 0 37.6 13.9 37.6 32.5s-16.9 32.5-37.6 32.5H164.7v162.3c0 20.7-14.8 37.6-33.4 37.6-18.2 0-33.4-16.9-33.4-37.6V130.7c0.5-18.6 17.8-32.5 38.5-32.5z m754.9 830.1H700.8c-20.7 0-37.6-14.8-37.6-33.4 0-18.6 16.9-33.4 37.6-33.4H863V699.2c0-20.7 13.9-37.6 32.5-37.6s32.5 16.9 32.5 37.6v194.9c0 18.6-16.1 34.2-36.7 34.2z m-754.9 0h190.5c20.7 0 37.6-14.8 37.6-33.4 0-18.6-16.9-33.4-37.6-33.4H164.7V699.2c0-20.7-14.8-37.6-33.4-37.6-18.2 0-33.4 16.9-33.4 37.6v194.9c0.5 18.6 17.8 34.2 38.5 34.2zM286 747h462.1V284.6H286V747z m51.1-411.3h360.3v360.5H337.1V335.7z m0 0" p-id="3500" fill="#3c8dbc"></path>
                        </svg>
                        <i class="fa fa-android fl p-5" aria-hidden="true" v-else></i>

                        <i class="fa fa-ellipsis-v fr pointer p-5 c-gray disableOperate" aria-hidden="true" 
                        @click="disableOperation" 
                        v-if="whitelist&& props.item.inwhitelist == 'false'"></i>
                        <el-dropdown trigger="click" class="fr pointer" size="small" @command="handleCommand" v-else>
                            <span class="el-dropdown-link">
                                <i class="fa fa-ellipsis-v p-5 c-primary" aria-hidden="true"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item :command="{appInfo: props.item, id: 'startApp'}">
                                    <i class="fa fa-hand-o-up m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.launch')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'stopApp'}"
                                :disabled="disableAppArray.indexOf(props.item.package)>-1">
                                    <i class="fa fa-hand-paper-o m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.stop')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'enableApp'}"
                                :disabled="whitelist|| props.item.isKioskApp == 'true'">
                                    <i class="fa fa-check-circle m-r-5" aria-hidden="true"></i>
                                    {{$t('appControl.enable')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'disableApp'}"
                                :disabled="disableAppArray.indexOf(props.item.package)>-1|| whitelist|| props.item.isKioskApp == 'true'">
                                    <i class="fa fa-ban m-r-5" aria-hidden="true"></i>
