                                    <path d="M891.3 98.2H700.8c-20.7 0-37.6 13.9-37.6 32.5s16.9 32.5 37.6 32.5H863v162.3c0 20.7 13.9 37.6 32.5 37.6s32.5-16.9 32.5-37.6V130.7c0-18.6-16.1-32.5-36.7-32.5z m-754.9 0h190.5c20.7 0 37.6 13.9 37.6 32.5s-16.9 32.5-37.6 32.5H164.7v162.3c0 20.7-14.8 37.6-33.4 37.6-18.2 0-33.4-16.9-33.4-37.6V130.7c0.5-18.6 17.8-32.5 38.5-32.5z m754.9 830.1H700.8c-20.7 0-37.6-14.8-37.6-33.4 0-18.6 16.9-33.4 37.6-33.4H863V699.2c0-20.7 13.9-37.6 32.5-37.6s32.5 16.9 32.5 37.6v194.9c0 18.6-16.1 34.2-36.7 34.2z m-754.9 0h190.5c20.7 0 37.6-14.8 37.6-33.4 0-18.6-16.9-33.4-37.6-33.4H164.7V699.2c0-20.7-14.8-37.6-33.4-37.6-18.2 0-33.4 16.9-33.4 37.6v194.9c0.5 18.6 17.8 34.2 38.5 34.2zM286 747h462.1V284.6H286V747z m51.1-411.3h360.3v360.5H337.1V335.7z m0 0" p-id="3500" fill="#bbbbbb"></path>
                                    </svg>
                                    <svg t="1571298273142" class="icon m-r-5"
                                    viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3499"
                                    width="12" height="12" v-else>
                                    <path d="M891.3 98.2H700.8c-20.7 0-37.6 13.9-37.6 32.5s16.9 32.5 37.6 32.5H863v162.3c0 20.7 13.9 37.6 32.5 37.6s32.5-16.9 32.5-37.6V130.7c0-18.6-16.1-32.5-36.7-32.5z m-754.9 0h190.5c20.7 0 37.6 13.9 37.6 32.5s-16.9 32.5-37.6 32.5H164.7v162.3c0 20.7-14.8 37.6-33.4 37.6-18.2 0-33.4-16.9-33.4-37.6V130.7c0.5-18.6 17.8-32.5 38.5-32.5z m754.9 830.1H700.8c-20.7 0-37.6-14.8-37.6-33.4 0-18.6 16.9-33.4 37.6-33.4H863V699.2c0-20.7 13.9-37.6 32.5-37.6s32.5 16.9 32.5 37.6v194.9c0 18.6-16.1 34.2-36.7 34.2z m-754.9 0h190.5c20.7 0 37.6-14.8 37.6-33.4 0-18.6-16.9-33.4-37.6-33.4H164.7V699.2c0-20.7-14.8-37.6-33.4-37.6-18.2 0-33.4 16.9-33.4 37.6v194.9c0.5 18.6 17.8 34.2 38.5 34.2zM286 747h462.1V284.6H286V747z m51.1-411.3h360.3v360.5H337.1V335.7z m0 0" p-id="3500" fill="#606266"></path>
                                    </svg>
                                    {{$t('appControl.setKiosk')}}
                                </el-dropdown-item>
                                <el-dropdown-item :command="{appInfo: props.item, id: 'cancelKiosk'}"
                                :disabled="disableAppArray.indexOf(props.item.package)>-1|| props.item.isKioskApp == 'false'">
                                    <svg t="1571735162764" class="icon m-r-5" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5560" 
                                        width="12" height="12" 
                                        v-if="disableAppArray.indexOf(props.item.package)>-1|| props.item.isKioskApp == 'false'">
                                        <path d="M853.333333 938.666667H682.666667v-85.333334h170.666666V682.666667h85.333334v256h-85.333334z m0-768H682.666667V85.333333h256v256h-85.333334V170.666667z m-682.666666 682.666666h170.666666v85.333334H85.333333V682.666667h85.333334v170.666666z m0-768h170.666666v85.333334H170.666667v170.666666H85.333333V85.333333h85.333334z m170.666666 256h341.333334v341.333334H341.333333V341.333333z" p-id="5561" fill="#bbbbbb"></path>
                                    </svg>
                                    <svg t="1571735162764" class="icon m-r-5" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5560" 
                                        width="12" height="12" v-else>
                                        <path d="M853.333333 938.666667H682.666667v-85.333334h170.666666V682.666667h85.333334v256h-85.333334z m0-768H682.666667V85.333333h256v256h-85.333334V170.666667z m-682.666666 682.666666h170.666666v85.333334H85.333333V682.666667h85.333334v170.666666z m0-768h170.666666v85.333334H170.666667v170.666666H85.333333V85.333333h85.333334z m170.666666 256h341.333334v341.333334H341.333333V341.333333z" p-id="5561" fill="#606266"></path>
                                    </svg>
                                    
                                    {{$t('appControl.cancelKiosk')}}
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                    <img :src="getSrc(props.item.raid)" :onerror="defaultImg" alt="no img" slot="card-body-img" slot-scope="props"/>
                </card-temp>       
            </el-tab-pane>
            <el-tab-pane name="appStatusHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('appControl.appManagerHistory')}}
                </span>
                <app-history
                :statusType="batchAppType"
                ref="appHistory"
                @getStatusCount="getBatchAppStatusCount"
                :groupname="groupname"
                @isFinished="isFinished"
                >
                </app-history>

            </el-tab-pane>
        </el-tabs>
         <el-dialog :visible.sync="dialogTasknameVisible" @close="initApkInfo()" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('appControl.batchInstallApk')}}
            </div>
            <el-form :model="appStatusForm" :rules="rules" ref="appStatusForm">
                <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                    <el-input v-model.trim="appStatusForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('appControl.installAppTaskName')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="batchInstallApp()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <div class="appStatusCount" v-show="isShowOverview">
            <span>{{$t('label.taskName')}} {{local_taskname}}</span>
            <span>{{$t('appControl.total')}} {{total}}</span>
            <span>{{$t('appControl.deploying')}} {{installing}}</span>
            <span>{{$t('appControl.success')}} {{success}}</span>
            <span>{{$t('appControl.failed')}} {{failed}}</span>
        </div>
    </div>
</template>

