<script>
    import {appControl, androidControl} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {showSystemAppArray, disableAppArray} from "@/assets/js/constant"

    import {getRepoAppsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi'
    import{getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"
    import {getRepoSrcApi} from "@/components/restfulapi/serverConfigApi"
    import {getDeviceStatus, setDeviceStatus, batchExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"

    import {singleInstallType, batchAppType} from "../../../assets/js/constant"
    import appHistory from "./history"
    import cardTemp from "../../../common/cardTemp"
    export default{
        name: 'batchAppcontrol',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "appOperation",
                appTableData: [],

                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                dialogTasknameVisible: false,
                //app status history
                filename: '',
                pkgname: '',
                version: '',
                address: '',
                versioncode: '',
                //batch installapp
                appStatusForm:{
                    taskname: '',
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },

                batchAppType: batchAppType,
                cid: "",
                appOptions: [],
                disableAppArray: disableAppArray,
                appType: "uninstallApp",
                whitelist: false,
                //installstatus count show
                isShowOverview: false,
                local_taskname: '',
                success: 0,
                installing: 0,
                failed: 0,
                listLoading: false,
                keywords: '',

            }
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        components: {
            appHistory,
            cardTemp
        },

        methods:{
            handleCommand(command){
                let appInfo=command.appInfo;
                this.cid = command.id;
                //package is diff in system and third
                if(this.appType === "system"|| this.appType==="desktop"){
                    this.address = "";
                    this.filename = appInfo.appName;
                    this.pkgname = appInfo.packageName;
                    this.version = appInfo.versionName;
                    this.versioncode = appInfo.versionCode;
                }else{
                    this.address = appInfo.address;
                    this.filename = appInfo.appname;
                    this.pkgname = appInfo.package;
                    this.version = appInfo.version;
                    this.versioncode = appInfo.versioncode;
                }

                this.appStatusForm.taskname = "";
                this.dialogTasknameVisible = true;

            },

            filterType(value, row){
                return row.type === value ;
            },

            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }
                let AppManagementInfoArray = [];
                let deviceAppArray = [];
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, appControl["getAllAppInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let apppackageinfo = JSON.parse(res.content.value);
                                let allAppData = apppackageinfo.data;
                                this.appOptions = allAppData;
                                if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                    allAppData.forEach((val) => {
                                        let uninstallAppObj = {
                                            type: 'uninstallApp',
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            version: val.versionName,
                                            address: "",
                                            raid: "",
                                            isKioskApp: val.isKioskApp,
                                            inwhitelist: val.inWhiteList,
                                        };
                                        if(val.type === "third"){
                                            AppManagementInfoArray.push(uninstallAppObj);
                                        }
                                        deviceAppArray.push(uninstallAppObj);                            
                                            
                                    })
                                }
                            }
                            this.getRepoApps(AppManagementInfoArray, deviceAppArray);  
                        }
                        
                          
                    })   
                })
            },
            
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps
            getRepoApps(AppManagementInfoArray, deviceAppArray){
                if(
                    Object.prototype.toString.call(AppManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(deviceAppArray) != "[object Array]"
                ){
                    console.error("getRepoAppsparameter is not array")
                    return;
                }
                let token;
                let InstallAppManagementInfo = {};
                let UpgradeAppManagementInfo = {};
                this.listLoading = true;
                getRepoAppsApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installappopt = "";
                            let upgradeappopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    InstallAppManagementInfo = {
                                        type : "installApp", 
                                        darkname: val.repo.darkname,
                                        //install apk name
                                        appname: val.filename, 
                                        package: val.pkgname, 
                                        versioncode: val.versioncode, 
                                        version: val.versionname,
                                        address: val.address,
                                        raid: val.raid,
                                        isKioskApp: "false",
                                        inwhitelist: "false",
                                        };
                                    AppManagementInfoArray.push(InstallAppManagementInfo);
                         
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // pop latest app info
                                            AppManagementInfoArray.pop();
                                            deviceAppArray[index].address = val.address;
                                            deviceAppArray[index].raid = val.raid;
                                            if (Number(val.versioncode) > Number(deviceapp_val.versioncode)){
                                                // remove install app  
                                                // this.removeObjInArray(AppManagementInfoArray, val.pkgname);
                                                UpgradeAppManagementInfo = {
                                                    type : "upgradeApp", 
                                                    darkname: val.repo.darkname,
                                                    appname: val.filename, 
                                                    package: val.pkgname, 
                                                    versioncode: val.versioncode, 
                                                    version: val.versionname,
                                                    address: val.address,
                                                    raid: val.raid,
                                                    isKioskApp: "false",
                                                    inwhitelist: "false",
                                                };
                                                    //push update app 
                                                AppManagementInfoArray.push(UpgradeAppManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                        this.appTableData = AppManagementInfoArray.reverse();
                    })
                    
                })
            },
	
            //batch install app
            batchSetAppSensor(cid, setSensorVal){
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();

                let appdata = {
                    groupname: this.groupname,
                    taskname: this.appStatusForm.taskname,
                    filename: this.filename,
                    pkgname: this.pkgname,
                    version: this.version,
                    versioncode: this.versioncode,
                    downloadpath: setSensorVal
                }  
                let taskname = this.appStatusForm.taskname;
                this.dialogTasknameVisible = false;                  
                batchExecDeviceStatus(appControl[cid], appdata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            //show overview
                            setLocal("local_taskname", taskname);
                            this.activeName = "appStatusHistory";
                            this.$refs.appHistory.intervalGetAppStatusHistory();
                        }else{
                            _g.handleError(res);
                        }
                    })
                    
                })        
                
                               
            },

            batchInstallApp(){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.appStatusForm.validate((valid) => {
                    if(valid) {
                        let apkDownloadAddress = this.address + "/" + this.filename;
                        this.batchSetAppSensor(this.cid, apkDownloadAddress);
                        
                    }
                })
            },
            
            selectBatchInstallApp(type, selectedAppData){
                this.cid = type;
                this.address = selectedAppData.address;
                this.filename = selectedAppData.appname;
                this.pkgname = selectedAppData.package;
                this.version = selectedAppData.version;
                this.versioncode = selectedAppData.versioncode;
                this.appStatusForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            initApkInfo(){
                this.filename = "";
                this.pkgname = "";
                this.version = "";
                this.address = "";
                this.appStatusForm.taskname = "";
                this.cid = "";
            },

             //get updatedp status count
            getBatchAppStatusCount(){
                let local_taskname = getLocal("local_taskname");
                if(!local_taskname){
                    console.error("getBatchAppStatusCount:localStorage(local_taskname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getAppStatusOverviewApi(local_taskname, batchAppType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.local_taskname = local_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName === "appStatusHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },
            
            initData(){
                this.appTableData = [];
            },

            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
            },

            handleClick(tab){
                if(tab.name === "appStatusHistory"){
                    this.getBatchAppStatusCount();
                }else{
                    this.isShowOverview = false;
                }
            },
            isFinished(){
                this.getSensorStatus();  
            },
            //get whitelist server status
            getServerStatus(){
                if(this.selectedAgentsData.length == 0){
                    console.error("getServerStatus: agentData is null");
                    return;
                }
                let sensor_key = "whitelist"
                getDeviceStatus(this.selectedAgentsData[0].value, androidControl[sensor_key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let status = res.content.value;
                            if(typeof(status) === "boolean"){
                                this[sensor_key] = status;
                            }else if(status === "true" || status === "false"){
                                this[sensor_key] = status === "true"? true: false;
                            }else{
                                this[sensor_key] = status;
                            }
                        }
                    })
                })
                
            },

            disableOperation(){
                this.$swal("", this.$t('appControl.appNotInWhitelist'), 'info', {button: this.$t('global.confirm')});
            },

            goAppMarket(){
                this.$router.push("/main/control/appcontrol");
            }
        },
       
        computed: {
            cardAppData(){
                if(this.appType === "uninstallApp"){
                    return this.appTableData;
                }else if(this.appType === "system" || this.appType=="desktop"){
                    return this.appOptions;
                }else{
                    return [];
                }
            },
            appTypeShow(){
                if(this.cardAppData.length >0){
                    return true;
                }else{
                    return false;
                }
                
            },

            subIntro(){
                if(this.appType === "uninstallApp"){
                    return ['appname', 'version'];
                }else if(this.appType === "system" || this.appType=="desktop"){
                    return ['appName', 'versionName'];
                }else{
                    return [];
                }
            },
            appTypeArray(){
                if(this.appType === "uninstallApp"){
                    return ['uninstallApp'];
                }else if(this.appType === "system"){
                    return ["system"];
                }else if(this.appType === "desktop"){
                    return ["true"]
                }else{
                    return []
                }
            },
            
            typeKey(){
                if(this.appType == "desktop"){
                    return "isDesktopApp"
                }else{
                    return "type"
                }
            },

            isShowSearchInput(){
                if(this.activeName === "appMarket"){
                    return true;
                }else{
                    return false;
                }
            },
            total(){
                return  this.success+ this.installing+ this.failed;
            }

        },
       
        watch: {
            isSingleMode(val){
                if(!val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                    this.getServerStatus();
                }
            }
        }

    }
</script>


